  display: flex;
  /* Chrome 29+, Firefox 22+, IE 11+, Opera 12.1/17/18, Android 4.4+ */
}
.dataShow .flex-wrap {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
