.dataShow .base-data .item {
  height: 2rem;
  margin: 10px 5px 5px 5px;
  background: #1A2833;
  border: 1px #48596D solid;
}
.dataShow .grad {
  /* Safari 5.1 - 6.0 */
  /* Opera 11.1 - 12.0 */
  /* Firefox 3.6 - 15 */
  background: -webkit-gradient(linear, left top, left bottom, from(#29D9C2), to(#1E68E4));
  background: linear-gradient(#29D9C2, #1E68E4);
