html {
	overflow: hidden;
}

* {
	padding: 0;
	margin: 0;
}

li {
	list-style: none;
}

body {
	background: #ededed;
	font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
	font-size: 12px;
	padding: 0;
}

.img img {
	width: 100%;
	display: block;
}

#top {
	position: fixed;
	top: 0;
	padding-top: 0.2rem;
	padding-bottom: 0.2rem;
	z-index: 5;
	width: 100%;
	background-color: #ededed;
	height: 0.8rem;
	line-height: 0.8rem;
}

#top .addr {
	height: 36px;
	margin: 0 auto;
	display: block;
}

#top .search {
	position: absolute;
	top: 0;
	right: 0;
	height: 36px;
	display: block;
}

#nav .swiper-slide span {
	margin: 0 5px;
	text-align: center;
	display: block;
	line-height: 2.5;
	font-size: 14px;
	color: rgba(101, 134, 164, 1);
}

#nav .bar {
	width: 50px;
	height: 3px;
	position: absolute;
	bottom: 0px;
}

#nav .bar .color {
	/*width:36px;*/
	margin: 0 auto;
	height: 2px;
	background: #29D9C2;
}

.banner img {
	width: 100%;
	display: block;
}

.banner .swiper-pagination {
	left: auto;
	right: 7px;
	bottom: 7px;
	width: auto;
	padding: 2px 7px;
	border-radius: 10px;
	color: #fff;
	background: rgba(0, 0, 0, .3);
}

#page {
	top: 1.2rem;
	bottom: 1rem;
	height: 100%;
}

#page .slidepage {
	height: 100%;
}

.scroll {
	height: 100%;
}

.slidescroll {
	height: auto;
}

.bottomClass {
	padding-top: 0.1rem;
	position: fixed;
	bottom: 0px;
	z-index: 11;
	width: 100%;
	background: #fff;
}

#footer {
	position: fixed;
	bottom: 0;
	z-index: 5;
}
